
"use client";

import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import Link from "next/link";
import { ArrowRight, BookOpenCheck } from "lucide-react";
import FeatureCards from "@/components/FeatureCards";
import { ModeToggle } from "@/components/Header";
import { motion, useMotionValue, useSpring, useTransform } from "framer-motion";
import React from "react";

export default function Home() {
  const x = useMotionValue(0);
  const y = useMotionValue(0);

  const mouseX = useSpring(x, { stiffness: 300, damping: 30, restDelta: 0.001 });
  const mouseY = useSpring(y, { stiffness: 300, damping: 30, restDelta: 0.001 });

  function handleMouseMove({ currentTarget, clientX, clientY }: React.MouseEvent<HTMLDivElement, MouseEvent>) {
    const { left, top, width, height } = currentTarget.getBoundingClientRect();
    const mouseXVal = clientX - left - width / 2;
    const mouseYVal = clientY - top - height / 2;
    // Set the motion values
    x.set(mouseXVal);
    y.set(mouseYVal);
  }

  const rotateX = useTransform(mouseY, [-200, 200], [10, -10]);
  const rotateY = useTransform(mouseX, [-400, 400], [-10, 10]);

  return (
    <div className="flex flex-col min-h-screen bg-transparent">
      <header className="p-4 bg-transparent absolute top-0 left-0 w-full z-10">
        <div className="container mx-auto flex items-center justify-between">
          <div className="flex items-center gap-2">
            <BookOpenCheck className="w-6 h-6 text-primary" />
            <Link href="/" className="text-xl font-bold text-foreground">
            CoachMatch
            </Link>
          </div>
          <div className="flex items-center gap-2">
            <nav className="hidden md:flex items-center gap-4">
                <Link href="/" className="text-sm font-medium transition-colors hover:text-primary text-muted-foreground">
                    Home
                </Link>
                <Link href="/about" className="text-sm font-medium transition-colors hover:text-primary text-muted-foreground">
                    About Us
                </Link>
            </nav>
            <ModeToggle />
          </div>
        </div>
      </header>
      <main className="flex-1 flex items-center justify-center p-4 sm:p-6 lg:p-8 relative overflow-hidden">
        <motion.div 
          className="w-full max-w-4xl mx-auto text-center z-0 relative"
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.5 }}
          style={{
            perspective: "1000px",
          }}
          onMouseMove={handleMouseMove}
          onMouseLeave={() => {
            x.set(0);
            y.set(0);
          }}
        >
          <motion.div
            style={{
              rotateX,
              rotateY,
              transformStyle: "preserve-3d",
            }}
          >
            <Card className="glowing-card">
              <CardHeader className="pb-6">
                <h1 className="text-4xl md:text-5xl font-extrabold tracking-tight text-gradient whitespace-nowrap">
                  Find Your Perfect Coaching Fit
                </h1>
                <CardDescription className="text-lg md:text-xl max-w-2xl mx-auto pt-2 text-muted-foreground">
                  Tired of one-size-fits-all advice? Take our quick, science-backed personality quiz to unlock personalized coaching recommendations tailored just for you.
                </CardDescription>
              </CardHeader>
              <CardContent className="py-6">
                  <FeatureCards />
              </CardContent>
              <CardFooter className="flex-col items-center justify-center pt-4">
                <Button asChild size="lg" className="text-lg">
                  <Link href="/quiz">
                    Start the Quiz <ArrowRight className="ml-2 h-5 w-5" />
                  </Link>
                </Button>
              </CardFooter>
            </Card>
          </motion.div>
        </motion.div>
      </main>
    </div>
  );
}
